<template>
  <div>
    <div class="popupBox">
      <img width="60px"
           height="60px"
           src="../../public/icons/logo.png"
           alt="">
    </div>
    <p>Go to <a style="color:rgb(102, 50, 210)"
         @click="openTwitch"
         href="#">twitch.tv</a></p>
    <hr>
    <three-link :url=baseUrl
                link1="Twiclips"
                link2="How to use"
                link3="Rate us 5"></three-link>
    <share :url=baseUrl></share>
    <ai-assistant-block></ai-assistant-block>
  </div>
</template>

<script>
import share from "../compontent/share.vue"
import threeLink from "../compontent/link.vue"
import AiAssistantBlock from "@/compontent/AiAssistantBlock.vue"
export default {
  data() {
    return {
      baseUrl: process.env.VUE_APP_WEBURL,
    }
  },
  components: {
    share,
    threeLink,
    AiAssistantBlock,
  },
  methods: {
    sendToAli(type, control, lable) {
      chrome.runtime.sendMessage(
        {
          action: "sendAli",
          type,
          control,
          lable,
        },
        function (res) {}
      )
    },
    openTwitch() {
      this.$ga.event("Popup_Jump", "Twitch").send()
      //console.log('twitch');
      setTimeout(() => {
        window.open("https://www.twitch.tv/")
      }, 100)
    },
  },
  mounted() {
    let that = this
    // 发送阿里云日志统计
    chrome.storage.local.get(["active_time"], function (res) {
      if (
        res.active_time === "undefined" ||
        new Date(Number(res.active_time) * 1000).toDateString() !==
          new Date().toDateString()
      ) {
        that.sendToAli("active", "user_active", "popup")
        chrome.storage.local.set({
          active_time: Math.round(new Date().getTime() / 1000).toString(),
        })
      }
    })
  },
}
</script>
