  watch: {
    isShowTwitchAiLogo() {
      this.sendToAli("popup", "ai_float_button", !!this.isShowTwitchAiLogo)
      chrome.storage.local.set({ isShowTwitchAiLogo: this.isShowTwitchAiLogo })
    },
  },
  methods: {
    sendToAli(type, control, label) {
      chrome.runtime.sendMessage(
        {
          action: "sendAli",
          type,
          control,
          label,
        },
        function () {}
      )
    },
    toAIExtension() {
      chrome.runtime.sendMessage(
        {
          action: "sendAliDiy",
          name: "click_dk_home",
          from: "popup",
        },
        function () {}
      )
      setTimeout(() => {
        window.open("https://vidsme.com/?utm_source=twiclips-ext", "_blank")
      }, 1000)
    },
  },
  created() {
    chrome.storage.local.get({ isShowTwitchAiLogo: true }, (res) => {
      this.isShowTwitchAiLogo = res.isShowTwitchAiLogo
    })
  },
}

